import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Question from './components/Question';
import Quiz from './components/Quiz';
import Result from './components/Result';
import quizQuestions from './api/quizQuestions';
import Introduction from './components/Introduction';
import Explanation from './components/Explanation';
import Skintone from './components/Skintone';
import ModelSelect from './components/ModelSelect';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      counter: 0,
      questionId: 1,
      question: '',
      answerOptions: [],
      answer: '',
      value: '',
      answersCount: {},
      result: '',
      pageId: 0
    };

    this.handleAnswerSelected = this.handleAnswerSelected.bind(this);
    this.handleContinueSelected = this.handleContinueSelected.bind(this);
    this.handleBackSelected = this.handleBackSelected.bind(this);
    this.handleBackQuizSelected = this.handleBackQuizSelected.bind(this);
  }

  componentDidMount() {
    const answerOptions = quizQuestions.map((question) => question.answers);  
  
    this.setState({
      question: quizQuestions[0].question,
      answerOptions: answerOptions[0]
    });
  }

  setUserAnswer(answer) {
    this.setState((state) => ({
      answersCount: {
        ...state.answersCount,
        [answer]: (state.answersCount[answer] || 0) + 1
      },
      answer: answer
    }));
  }

  setPreviousQuestion() {
    const counter = this.state.counter - 1;
    const questionId = this.state.questionId - 1;
    this.setState({
      counter: counter,
      questionId: questionId,
      question: quizQuestions[counter].question,
      answerOptions: quizQuestions[counter].answers,
      answer: ''
    });
  }

  setNextQuestion() {
    const counter = this.state.counter + 1;
    const questionId = this.state.questionId + 1;
    this.setState({
      counter: counter,
      questionId: questionId,
      question: quizQuestions[counter].question,
      answerOptions: quizQuestions[counter].answers,
      answer: ''
    });
  }

  getResults() {
    const answersCount = this.state.answersCount;
    const answersCountKeys = Object.keys(answersCount);
    const answersCountValues = answersCountKeys.map((key) => answersCount[key]);
    const maxAnswerCount = Math.max.apply(null, answersCountValues);
  
    return answersCountKeys.filter((key) => answersCount[key] === maxAnswerCount);
  }

  setResults (result) {
    if (result.length === 1) {
      this.setState({ result: result[0] });
    } else {
      this.setState({ result: 'Undetermined' });
    }
  }

  handleAnswerSelected(event) {
    event.preventDefault();
    this.setUserAnswer(event.currentTarget.value);
    if (this.state.questionId < quizQuestions.length) {
        this.setNextQuestion();
      } else {
        this.setState({ pageId: this.state.pageId + 1 });
      }
  }

  handleContinueSelected(event) {
    this.setState({ pageId: this.state.pageId + 1 });
  }

  handleBackSelected(event) {
    this.setState({ pageId: this.state.pageId - 1 });
  }

  handleBackQuizSelected(event) {
    if (this.state.questionId == 1) {
      this.setState({ pageId: this.state.pageId - 1 });
    } else {
      this.setPreviousQuestion();
    }
  }

  renderIntroduction() {
    return (
      <Introduction
        onContinueSelected={this.handleContinueSelected}
      />
    );
  }

  renderExplanation() {
    return (
      <Explanation
        onContinueSelected={this.handleContinueSelected}
        onBackSelected={this.handleBackSelected}
      />
    )
  }

  renderSkintone() {
    return (
      <Skintone
        onContinueSelected={this.handleContinueSelected}
        onBackSelected={this.handleBackSelected}
      />
    )
  }

  renderModelSelect() {
    return (
      <ModelSelect
        onContinueSelected={this.handleContinueSelected}
        onBackSelected={this.handleBackSelected}
      />
    )
  }

  renderQuiz() {
    return (
      <Quiz
        answer={this.state.answer}
        value={this.state.value}
        answerOptions={this.state.answerOptions}
        questionId={this.state.questionId}
        question={this.state.question}
        questionTotal={quizQuestions.length}
        onAnswerSelected={this.handleAnswerSelected}
        onBackQuizSelected={this.handleBackQuizSelected}
      />
    );
  }
  
  renderResult() {
    return (
      <Result
        quizResult={this.state.result}
        onBackSelected={this.handleBackSelected}
      />
    );
  }

  renderPage() {
    switch(this.state.pageId) {
      case 0:
        return this.renderIntroduction();
      case 1:
        return this.renderExplanation();
      case 2:
        return this.renderSkintone();
      case 3:
        return this.renderQuiz();
      case 4:
        return this.renderModelSelect();
      case 5:
        return this.renderResult();
      default:
        return this.renderIntroduction();
    }
  }
  
  render() {
    return (
      <div className="App">
        { this.renderPage() }
      </div>
    )
  }
}

export default App;
