import React from 'react';
import PropTypes from 'prop-types';
import Question from '../components/Question';
import QuestionCount from '../components/QuestionCount';
import AnswerOption from '../components/AnswerOption';
import { CSSTransitionGroup } from 'react-transition-group';

function Quiz(props) {
    function renderAnswerOptions(key) {
        return (
          <AnswerOption
            key={key.content}
            answerContent={key.content}
            answerType={key.type}
            answer={props.answer}
            value={props.value}
            questionId={props.questionId}
            onAnswerSelected={props.onAnswerSelected}
          />
        );
      }
    return ( 
      <CSSTransitionGroup
      className="container"
      component="div"
      transitionName="fade"
      transitionEnterTimeout={2000}
      transitionLeaveTimeout={1500}
      transitionAppear
      transitionAppearTimeout={1500}
      >
      <div key={props.questionId} id="wrapper" className="pt_category_landing_nonav pt_shadefinder FentyFace2">
        <main className="clearfix">
          <div id="browser-check">
            <noscript>
              <div className="browser-compatibility-alert">
                <p className="browser-error">Your browser's Javascript functionality is turned off. Please turn it on so that you can experience the full capabilities of this site.</p>
              </div>
            </noscript>
          </div>
          <div id="primary" className="primary-content">
            <div className="finderholder" style={{ minWidth: 1519 + 'px', maxWidth: 1519 + 'px' }}>
              <div className="slideContainer step default" id="no-q3" style={{ width: 1519 + 'px' }}>
                <div className="content-wrapper">
                  <div className="hexBg">
                    <style type="text/css">
                    </style>
                    <div id="product-finder-no-q3" className="content-asset  ">
                      <div className="questionHead">
                        <h2 className="finderHeader">Bovanti Face Shade Finder</h2>
                        <div className="progressBarHolder">
                          <style type="text/css">
                          </style>
                          <div id="product-finder-progress-bar" className="content-asset  ">
                            <div className="progressBar">
                              <span className="bar">
                                <span className="fill" style={{ width: 36.3636 + '%' }}/>
                              </span>
                            </div>
                          </div>
                        </div>
                        <div tabindex="0" className="questionText">
                          <strong><Question content={props.question} /></strong>
                        </div>
                      </div>
                    </div>
                    <style type="text/css">
                    </style>
                    <div id="product-finder-no-q3-answers" className="content-asset  ">
                      <form id="stepForm">
                        <div className="answerOptions">
                          {props.answerOptions.map(renderAnswerOptions)}
                        </div>
                      </form>
                      <div className="btnHolder sticky wrapper buttons absolute">
                        <div className="back">
                          <a onClick={ props.onBackQuizSelected } tabindex="0" aria-label="Go to previous step" className="btnBack">
                            <span>Back</span>
                            <svg id="color-fill" xmlns="http://www.w3.org/2000/svg" version="1.1" width="100%" viewBox="0 0 300 280">
                              <polygon className="hex" points="300,150 225,280 75,280 0,150 75,20 225,20"/>
                            </svg>
                          </a>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="screenreader-text" aria-live="polite">Continue button is currently disabled until a selection is made</div>
            </div>
          </div>
        </main>
      </div>
      </CSSTransitionGroup>
    );
  }
  
  Quiz.propTypes = {
    answer: PropTypes.string.isRequired,
    value: PropTypes.string.isRequired,
    answerOptions: PropTypes.array.isRequired,
    counter: PropTypes.number.isRequired,
    question: PropTypes.string.isRequired,
    questionId: PropTypes.number.isRequired,
    questionTotal: PropTypes.number.isRequired,
    onAnswerSelected: PropTypes.func.isRequired,
    onBackQuizSelected: PropTypes.func.isRequired
  };
  
  export default Quiz;