import React from 'react';
import PropTypes from 'prop-types';
import { CSSTransitionGroup } from 'react-transition-group';
import '../index.css';

function Skintone(props) {
  return (
    <CSSTransitionGroup
    className="container"
    component="div"
    transitionName="fade"
    transitionEnterTimeout={2000}
    transitionLeaveTimeout={1500}
    transitionAppear
    transitionAppearTimeout={1500}
    >
    <div id="wrapper" class="pt_category_landing_nonav pt_shadefinder FentyFace2">
        <main class="clearfix">
            <div id="browser-check">
                <noscript>
                    <div class="browser-compatibility-alert">
                        <p class="browser-error">Your browser's Javascript functionality is turned off. Please turn it on so that you can experience the full capabilities of this site.</p>
                    </div>
                </noscript>
            </div>
            <div id="primary" class="primary-content">
                <div className="finderholder" style={{ minWidth: 1519 + 'px', maxWidth: 1519 + 'px' }}>
                <div className="screenreader-text" aria-live="polite">Continue button is currently disabled until a selection is made</div>
                <div className="slideContainer step default" id="no-q2" style={{ width: 1519 + 'px' }}>
                    <div className="content-wrapper">
                        <div className="roseBg sliders">
                            <style type="text/css">
                            </style>
                            <div id="product-finder-no-q2" className="content-asset  ">
                                <div className="questionHead">
                                    <h2 className="finderHeader">Bovanti Face Shade Finder</h2>
                                    <div className="progressBarHolder">
                                        <style type="text/css">
                                        </style>
                                        <div id="product-finder-progress-bar" className="content-asset  ">
                                            <div className="progressBar">
                                                <span className="bar">
                                                    <span className="fill" style={{ width: 27.2727 + '%' }}/>
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                    <div tabindex="0" className="questionText">
                                        <strong style={{ color: '#dcbb40' }}>Select the model closest to your skin tone to find <br className="desktop"/> your shade range.</strong>
                                    </div>
                                </div>
                            </div>
                            <style type="text/css">
                            </style>
                            <div id="product-finder-no-q2-answers" className="content-asset  ">
                                <form id="stepForm">
                                    <div className="answerOptions">
                                        <div className="slick option slick-slider slick-initialized slick-accessible" data-value="light" tabindex="1" aria-label="Click to select light shade" role="presentation">
                                            <div aria-live="polite" className="slick-list draggable">
                                                <div className="slick-track" style={{ opacity: 1, width: 756 + 'px' }}>
                                                    <div className="option-wrap slick-slide slick-current slick-active selected" style={{ width: 252 + 'px' }} role="group" data-slick-index="0" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0100_MODEL.jpg"/>
                                                    </div>
                                                    <div className="option-wrap slick-slide slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="1" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0150_MODEL.jpg"/>
                                                    </div>
                                                    <div className="option-wrap slick-slide slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="2" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0190_MODEL.jpg"/>
                                                    </div>
                                                </div>
                                            </div>
                                            <div aria-live="polite" aria-atomic="true" className="live-region slick-visually-hidden"/>
                                        </div>
                                        <span className="sliderLabel">Light</span>
                                        <div className="slick option slick-slider slick-initialized slick-accessible" data-value="medium" tabindex="2" aria-label="Click to select medium shade" role="presentation">
                                            <div aria-live="polite" className="slick-list draggable">
                                                <div className="slick-track" style={{ opacity: 1, width: 756 + 'px' }}>
                                                    <div className="option-wrap slick-slide slick-current slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="0" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0210_MODEL.jpg"/>
                                                    </div>
                                                    <div className="option-wrap slick-slide slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="1" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0240_MODEL.jpg"/>
                                                    </div>
                                                    <div className="option-wrap slick-slide slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="2" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0280_MODEL.jpg"/>
                                                    </div>
                                                </div>
                                            </div>
                                            <div aria-live="polite" aria-atomic="true" className="live-region slick-visually-hidden"/>
                                        </div>
                                        <span className="sliderLabel">Medium</span>
                                        <div className="slick option slick-slider slick-initialized slick-accessible" data-value="tan" tabindex="3" aria-label="Click to select tan shade" role="presentation">
                                            <div aria-live="polite" className="slick-list draggable">
                                                <div className="slick-track" style={{ opacity: 1, width: 756 + 'px' }}>
                                                    <div className="option-wrap slick-slide slick-current slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="0" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0310_MODEL.jpg"/>
                                                    </div>
                                                    <div className="option-wrap slick-slide slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="1" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0360_MODEL.jpg"/>
                                                    </div>
                                                    <div className="option-wrap slick-slide slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="2" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0390_MODEL.jpg"/>
                                                    </div>
                                                </div>
                                            </div>
                                            <div aria-live="polite" aria-atomic="true" className="live-region slick-visually-hidden"/>
                                        </div>
                                        <span className="sliderLabel">Tan</span>
                                        <div className="slick option slick-slider slick-initialized slick-accessible" data-value="deep" tabindex="4" aria-label="Click to select deep shade" role="presentation">
                                            <div aria-live="polite" className="slick-list draggable">
                                                <div className="slick-track" style={{ opacity: 1, width: 756 + 'px' }}>
                                                    <div className="option-wrap slick-slide slick-current slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="0" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0410_MODEL.jpg"/>
                                                    </div>
                                                    <div className="option-wrap slick-slide slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="1" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0440_MODEL.jpg"/>
                                                    </div>
                                                    <div className="option-wrap slick-slide slick-active" style={{ width: 252 + 'px' }} role="group" data-slick-index="2" aria-hidden="false">
                                                        <img src="https://www.fentybeauty.com/on/demandware.static/-/Sites-itemmaster_FNT/default//product-bg/FB30006_FB0490_model.jpg"/>
                                                    </div>
                                                </div>
                                            </div>
                                            <div aria-live="polite" aria-atomic="true" className="live-region slick-visually-hidden"/>
                                        </div>
                                        <span className="sliderLabel">Deep</span>
                                    </div>
                                </form>
                                <div className="btnHolder sticky wrapper buttons fixed">
                                    <div className="back">
                                        <a onClick={ props.onBackSelected } tabindex="0" aria-label="Go to previous step" className="btnBack">
                                            <span>Back</span>
                                            <svg id="color-fill" xmlns="http://www.w3.org/2000/svg" version="1.1" width="100%" viewBox="0 0 300 280">
                                                <polygon className="hex" points="300,150 225,280 75,280 0,150 75,20 225,20"/>
                                            </svg>
                                        </a>
                                    </div>
                                    <div className="next">
                                        <a onClick={ props.onContinueSelected } tabindex="0" aria-label="Proceed to next step" className="btnNext" aria-hidden="true">
                                            <span>Next</span>
                                            <svg id="color-fill" xmlns="http://www.w3.org/2000/svg" version="1.1" width="100%" viewBox="0 0 300 280">
                                                <polygon className="hex" points="300,150 225,280 75,280 0,150 75,20 225,20"/>
                                            </svg>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            </div>
        </main>
    </div>
    </CSSTransitionGroup>
  );
}

Skintone.propTypes = {
    onContinueSelected: PropTypes.func.isRequired,
    onBackSelected: PropTypes.func.isRequired
};

export default Skintone;