import React from 'react';
import PropTypes from 'prop-types';
import { CSSTransitionGroup } from 'react-transition-group';

function Introduction(props) {
  return (
    <CSSTransitionGroup
    className="container"
    component="div"
    transitionName="fade"
    transitionEnterTimeout={2000}
    transitionLeaveTimeout={1500}
    transitionAppear
    transitionAppearTimeout={1500}
    >
    <div id="wrapper" className="pt_category_landing_nonav pt_shadefinder FentyFace2">
        <main className="clearfix">
			<div id="primary" className="primary-content">
				<div className="finderholder" style={{minWidth: 1519 + 'px', maxWidth: 1519 + 'px'}}>
					<div className="slideContainer landing backgroundAndLogoImages" id="fentyfaceLanding" style={{width: 1519 + 'px'}}>
                        <div className="content-wrapper">
                            <div className="splashcopy">
                                <div id="hexagon_wrapper">
                                    <svg id="color-fill" xmlns="http://www.w3.org/2000/svg" version="1.1" width="100%" viewBox="0 0 300 280">
                                        <polygon className="hex" points="300,150 225,280 75,280 0,150 75,20 225,20"/>
                                    </svg>
                                </div>
                                <div id="landing_text_wrapper">
                                    <div id="landing_text">
                                        <h1 className="finder-heading">
                                            Bovanti&nbsp;Face Shade&nbsp;Finder
                                        </h1>
                                        <div className="landing-copy">
                                            Find your Bovanti foundation&nbsp;shade.
                                        </div>
                                        <div className="btnHolder">
                                            <a onClick={props.onContinueSelected} className="button btnNext">Take Quiz Now</a>
                                        </div>
                                    </div>
                                </div>
                                <div id="column1">
                                    <div className="all_landing_images mobile" id="landing_Deep_Model">
                                        <img alt="Fenty Face finder Landing Page Deep Model" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dw83582872/fentyface2/landing/mobile/22513_DEEP_MODEL_116_1.png" title="Fenty Face finder Landing Page Deep Model" />
                                    </div>
                                    <div className="all_landing_images mobile" id="landing_Light_Model">
                                        <img alt="Fenty Face finder Landing Page Light Model" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dw7fc94251/fentyface2/landing/mobile/22513_LIGHT_MODEL_120_1.png" title="Fenty Face finder Landing Page LightModel" />
                                    </div>
                                    <div className="all_landing_images mobile" id="landing_Tan_Model">
                                        <img alt="Fenty Face finder Landing Page Tan Model" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dwcaa3e25a/fentyface2/landing/mobile/FB30002_FB3005_TAN2_MODEL_065.png" title="Fenty Face finder Landing Page Tan Model" />
                                    </div>
                                    <div className="all_landing_images mobile" id="landing_Swatch1">
                                        <img alt="Fenty Face finder Landing Page Swatch1" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dwc724203b/fentyface2/landing/mobile/FB30004_FB3033_swatchv1.png" title="Fenty Face finder Landing Page Swatch1" />
                                    </div>
                                    <div className="all_landing_images mobile" id="landing_Swatch2">
                                        <img alt="Fenty Face finder Landing Page Swatch2" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dw3eb307e6/fentyface2/landing/mobile/FB30006_FB0498_swatch.png" title="Fenty Face finder Landing Page Swatch2" />
                                    </div>
                                    <div className="all_landing_images desktop" id="landing_Swatch3">
                                        <img alt="Fenty Face finder Landing Page Swatch3" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dw4f72d290/fentyface2/landing/desktop/FB_SETTING_POWDER_CASHEWv1.png" title="Fenty Face finder Landing Page Swatch3" />
                                    </div>
                                    <div className="all_landing_images desktop" id="landing_Swatch4">
                                        <img alt="Fenty Face finder Landing Page Swatch4" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dw78bce5d5/fentyface2/landing/desktop/YACHT_LYFE-bottom-leftv1.png" title="Fenty Face finder Landing Page Swatch4" />
                                    </div>
                                </div>
                                <div id="column2" className="desktop">
                                    <div className="all_landing_images desktop" id="landing_Swatch5">
                                        <img alt="Fenty Face finder Landing Page Swatch5" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dwb0a76c75/fentyface2/landing/desktop/STARSTRUCK-bottom-halfv1.png" title="Fenty Face finder Landing Page Swatch5" />
                                    </div>
                                    <div className="all_landing_images desktop" id="landing_Swatch6">
                                        <img alt="Fenty Face finder Landing Page Swatch6" src="https://i.ibb.co/Dtd37Sj/1.png" title="Fenty Face finder Landing Page Swatch6" />
                                    </div>
                                    <div className="all_landing_images desktop" id="landing_Swatch7">
                                        <img alt="Fenty Face finder Landing Page Swatch7" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dw277159c8/fentyface2/landing/desktop/FB30006_FB0498_swatchv1.png" title="Fenty Face finder Landing Page Swatch7" />
                                    </div>
                                </div>
                                <div id="column3" className="desktop">
                                    <div className="all_landing_images desktop" id="landing_Swatch8">
                                        <img alt="Fenty Face finder Landing Page Swatch8" src="https://i.ibb.co/7k182dM/3.png" title="Fenty Face finder Landing Page Swatch8" />
                                    </div>
                                    <div className="all_landing_images desktop" id="landing_Swatch9">
                                        <img alt="Fenty Face finder Landing Page Swatch9" src="https://i.ibb.co/dm3L9RV/2.png" title="Fenty Face finder Landing Page Swatch9" />
                                    </div>
                                </div>
                                <div id="column4" className="desktop">
                                    <div className="all_landing_images desktop" id="landing_Swatch10">
                                        <img alt="Fenty Face finder Landing Page Swatch10" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dw00686bbc/fentyface2/landing/desktop/44558_Concealer_Smear_2000x2500_RGB_360v1.png" title="Fenty Face finder Landing Page Swatch10" />
                                        <div id="concealer1"/>
                                    </div>
                                    <div className="fidycent"/>
                                    <div className="all_landing_images desktop" id="landing_Swatch11">
                                        <img alt="Fenty Face finder Landing Page Swatch11" src="https://www.fentybeauty.com/on/demandware.static/-/Sites-FENTY-Library/default/dw7ea6fc4a/fentyface2/landing/desktop/KILLAWATT-trianglev2.png" title="Fenty Face finder Landing Page Swatch11" />
                                        <div id="concealer2"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
					<div className="screenreader-text" aria-live="polite">Continue button is currently disabled until a selection is made</div>
                </div>
            </div>
        </main>
    </div>
    </CSSTransitionGroup>
  );
}

Introduction.propTypes = {
    onContinueSelected: PropTypes.func.isRequired
};

export default Introduction;