import React from 'react';
import PropTypes from 'prop-types';
import { CSSTransitionGroup } from 'react-transition-group';

function Result(props) {
  return (
    <CSSTransitionGroup
    className="container"
    component="div"
    transitionName="fade"
    transitionEnterTimeout={2000}
    transitionLeaveTimeout={1500}
    transitionAppear
    transitionAppearTimeout={1500}
    >
    <div id="wrapper" className="pt_category_landing_nonav pt_shadefinder FentyFace2">
      <main className="clearfix">
        <div id="browser-check">
          <noscript>
            <div className="browser-compatibility-alert">
              <p className="browser-error">Your browser's Javascript functionality is turned off. Please turn it on so that you can experience the full capabilities of this site.</p>
            </div>
          </noscript>
        </div>
        <div id="primary" className="primary-content">
          <div className="finderholder" style={{ minWidth: 1519 + 'px', maxWidth: 1519 + 'px' }}>
            <div className="screenreader-text" aria-live="polite">Next button is now selectable</div>
            <div className="slideContainer results chgBtnToAdded default fenty-finder-results" id="results" style={{ width: 1519 + 'px' }}>
              <div className="content-wrapper js-stickybit-parent">
                <div className="resultTop">
                  <div className="header">
                    <style type="text/css">
                    </style>
                    <div id="product-finder-results-header" className="content-asset  ">
                      <div className="btnHolder wrapper buttons">
                        <div className="back">
                          <a onClick={ props.onBackSelected } className="btnBack">
                            <span>&lt; Back</span>
                          </a>
                        </div>
                      </div>
                      <h1 className="skipScrollBack chgBtnToAdded">Get your Bovanti face on</h1>
                      <p className="b-head"/>
                    </div>
                  </div>
                  <div className="primary-image">
                    <picture className="" itemprop="image" itemscope="" itemtype="http://schema.org/ImageObject">
                      <div className="imageWrapper">
                        <img data-loadplaceholder="true" data-aspecth="NaN" data-aspectw="null" data-srcset="https://i.imgur.com/j9qVHJw.jpg" data-src="https://i.imgur.com/j9qVHJw.jpg" loading="lazy" className="primary-image lazyloaded" id="" alt="" title="PRO FILT'R SOFT MATTE LONGWEAR FOUNDATION, #120" itemprop="image" src="https://i.imgur.com/j9qVHJw.jpg" style={{ height: 'inherit' }}/>
                        <meta itemprop="contentUrl" content="https://i.imgur.com/j9qVHJw.jpg"/>
                      </div>
                    </picture>
                  </div>
                  <div className="detailsCta">
                    <div className="beforeCta">
                      <style type="text/css">
                      </style>
                      <div id="product-finder-results-header-before-cta" className="content-asset  ">
                        <h2 className="your-results">With Our Foundation Stick + Concealor</h2>
                      </div>
                      <span className="results-shade-number-holder"> Cocoa </span>
                    </div>
                    <form action="https://www.fentybeauty.com/search?dwcont=C578865137" method="post" id="dwfrm_product_addtocart_d0nrnfgcufkc" className="pdpForm" novalidate="novalidate">
                      <input type="hidden" name="cartAction" id="cartAction" value="add"/>
                      <input type="hidden" name="pid" id="pid" value="22865"/>
                      <div className="availability">
                        <span className="label">Availability:&nbsp;</span>
                        <span className="value">
                          <div className="availability-msg IN_STOCK ">
                            <p className="in-stock-msg">In Stock</p>
                            <link itemprop="availability" href="http://schema.org/InStock"/>
                            <input type="hidden" name="availability" value="in-stock"/>
                          </div>
                        </span>
                      </div>
                      <div className="cta-holder">
                        <button className="emailBtn btn">Email Results</button>
                        <button name="topresults" className=" add-all-top-to-cart btn" data-bis-title="Email Me When In Stock" title="Add To Bag">Add To Bag</button>
                      </div>
                      <div className="product-actions">
                        <ul className="menu">
                          <li>
                            <a className="wl-action wishlist wishlist-not-logged-in js-add-to-wishlist" data-pid="22865" title="Opens a dialog for your wishlist" href="https://www.fentybeauty.com/on/demandware.store/Sites-FENTY-Site/default/Wishlist-LoginModal">
                              <span className="wishlist-svg">
                                <svg width="16px" height="16px" viewBox="0 0 16 16" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                  <title>Heart</title>
                                  <defs/>
                                  <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                                    <g id="heart_empty-9481" fill-rule="nonzero" fill="#000000">
                                      <path d="M8,16 L0,7.48453608 L0,1.81443299 L3.52405063,0 L8,2.55670103 L12.5164557,0.0206185567 L16,1.81443299 L16,7.48453608 L8,16 Z M0.810126582,7.15463918 L8,14.8041237 L15.1898734,7.15463918 L15.1898734,2.32989691 L12.5367089,0.969072165 L8,3.50515464 L3.52405063,0.948453608 L0.830379747,2.32989691 L0.830379747,7.15463918 L0.810126582,7.15463918 Z" id="Shape-8698"/>
                                    </g>
                                  </g>
                                </svg>
                              </span> Add to Wishlist</a>
                          </li>
                        </ul>
                      </div>
                    </form>
                    <form action="https://www.fentybeauty.com/search?dwcont=C578865137" method="post" id="dwfrm_product_addtocart_d0qxpywdymsj" className="pdpForm hidden" novalidate="novalidate">
                      <input type="hidden" name="cartAction" id="cartAction" value="add"/>
                      <input type="hidden" name="pid" id="pid" value="35627"/>
                    </form>
                    <div className="afterCta">
                      <style type="text/css">
                      </style>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
    </CSSTransitionGroup>
  );
}

Result.propTypes = {
  quizResult: PropTypes.string.isRequired,
  onBackSelected: PropTypes.func.isRequired
};

export default Result;